.container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}

// 480+
@media screen and (min-width: 480px) {
    .container {
        width: 480px;
    }
}

// 768+
@media screen and (min-width: 768px) {
    .container {
        width: 768px;
    }
}

// 1680+
@media screen and (min-width: 1680px) {
    .container {
        width: 1680px;
    }
}